<template>
    <!-- 生产异常 -->
    <div class="heigth-div scrollBar">
        <div class="point-div">
            <span class="spanI">172号车超过10分钟未进磅</span>
            <span class="spanII">2018-3-27 14:30</span>
        </div>
        <div class="point-div">
            <span class="spanI">001号线生产超时一小时</span>
            <span class="spanII">2018-3-27 14:30</span>
        </div>
    </div>
    <!-- <div class="heigth-div percent-table" ref="wrapHeight">
        <el-table
            :data="abnormalSendData"
            :height="tabHeight"
            border
            highlight-current-row
            class="th-center tab-pad">
            <el-table-column prop="sendnum" label="送货单号" align="center" min-width="2"></el-table-column>
            <el-table-column prop="senddate" label="送货日期" align="center" min-width="2" :formatter="format"></el-table-column>
            <el-table-column prop="cancelname" label="送货单取消原因" align="center" min-width="5"></el-table-column>
        </el-table>
    </div> -->
</template>
<script>
export default {
    props:['wrapHeight'],
    data(){
        return {
            apiUrl:'/finance/erpMes/erp-prod-send',
            tabHeight:205,
            abnormalSendData:[],
            someData:[],
            content:'senddate_ge_date='+this.recentDate(0)+'&senddate_le_date='+this.recentDate(1)+'&sendstatus=-1',
            //content:'sendstatus=-1',
        }
    },
    activated(){
        //this.init();
    },
    mounted() {
        //this.init();
    },
    methods: {
        init() {
            //this.initEchart();
            // this.setEchart();
            this.getlist();
            this.getSomeData();
            this.getDivHeight();
        },

        getDivHeight() {
            this.tabHeight = this.$refs.wrapHeight.offsetHeight;
        },

        //获取数据
        getlist(){
            let tempData = [];
            this.abnormalSendData = [];
            this.someData = [];
            this.getAjax(this.apiUrl+'?'+'pageNo=1'+'&pageSize=1000'+'&'+this.content)
                .then(result => {
                    if (result.status === 200) {
                        if(result.data.list[0] != null){
                            tempData = _.initial(result.data.list);
                            for(let i=0; i<tempData.length; i++){
                                if(tempData[i].canceltype === null){
                                    tempData[i].cancelname = '其他原因'
                                }else {
                                    for(let j=0; j<this.someData.length; j++){
                                        if(tempData[i].canceltype === this.someData[j].itemvalue){
                                            tempData[i].cancelname = this.someData[j].itemname
                                        }
                                    }
                                }
                            }
                            this.abnormalSendData =  tempData;
                        }
                    }else{
                        this.$message({
                            message: '系统正在升级中，请稍后再试',
                            type: 'error'
                        });
                    }
                })
        },

        //获取数据字典
        getSomeData() {
            this.postDicData([{'nodekey':'ErpProd_SendCancelType'}]).then( (result)=>{
                if (result.status === 200) {
                    this.someData = result.data['ErpProd_SendCancelType'];      
                }
            })
        },

        //日期显示格式化  ErpProd_SendCancelType
        format(row,column,cellValue) {
            return this.formatDate(cellValue,'ymdhM');
        },
    }
}
</script>
<style scoped lang="less" type="text/less">
    .point-div{
        margin-top: 10px;
        margin-left: 10px;
        display: flex;
        justify-content: space-between;
        .spanII{
            margin-right: 10px;
        }
    }
    .heigth-div{
        height: 95%;
        overflow-y:auto
    }
</style>


